/********************
 1.GENERAL
 ********************/
.btn {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  outline: none;
  transition: all 0.3s;
  padding: 0 1.5625rem;
  margin: 0 0 0.3125rem 0;
  height: 2.5rem;
  border-width: 0.0625rem;
  border-style: solid;
  text-align: center;
  font-size: 1.25rem;
  line-height: 2.375rem;
  border-color: #e87542;
  background-color: #e87542;
  color: #fff; }
  .btn:hover {
    border-color: #dc561b;
    background-color: #dc561b;
    color: #fff; }
  .btn.hvr-invert:hover {
    border-color: #e87542;
    background-color: transparent;
    color: #e87542; }
  .btn.type-hollow {
    border-color: #e87542;
    background-color: transparent;
    color: #e87542; }
    .btn.type-hollow:hover {
      border-color: #dc561b;
      background-color: transparent;
      color: #dc561b; }
    .btn.type-hollow.hvr-invert:hover {
      border-color: #e87542;
      background-color: #e87542;
      color: #fff; }
  .btn.shape-rnd {
    border-radius: 2.5rem; }
  .btn.shape-crnr {
    border-radius: 0.1875rem; }

a.btn, button.btn, div.btn {
  cursor: pointer; }

/********************
 2.SIZES
