}
/* END MEDIA QUERIES max-width 768px - TABLETS  */

/* MEDIA QUERIES max-width 767px - MOBILE LANDSCAPE  */
@media (max-width: 767px) {

    #top_line {
        display: none;
    }

    .strip_all_tour_list {
        text-align: center;
    }

    .img_list {
        width: 100%;
        margin: auto;
    }

    .img_list img {
        left: 0;
        width: 100%;
        height: auto;
        top: -30%;
    }

    .tour_list_desc {
        padding: 0 15px;
        border-right: none;
        height: auto;
    }

    .price_list {
        display: block;
        height: auto;
        margin-left: 0;
        text-align: center;
        padding: 25px 0 15px 0;
    }

    .price_list div {
        display: block;
        text-align: center;
    }

    .price_list .button {
        display: block;
        margin: 15px;
    }

    .main_title {
        font-size: 14px;
    }

    .main_title h2 {
        font-size: 24px;
    }

    .main_title p {
        font-size: 16px
    }

    /* Mobile navigation for categories left column */
    .styled-select-cat select {
        background: transparent;
        width: 107%;
        padding: 7px 5px 5px 45px;
        border: 0;
        border-radius: 0;
        height: 50px;
        margin: 0;
        font-weight: 400;
        -moz-appearance: window;
        -webkit-appearance: none;
        cursor: pointer;
        color: #888;
    }

    .styled-select-cat {
        width: 100%;
        overflow: hidden;
        height: 50px;
        position: relative;
        border: none;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
    }

    .styled-select-cat:before {
        font-style: normal;
        font-weight: normal;
        font-family: "icon_set_1";
        font-size: 22px;
        content: "\53";
        position: absolute;
        left: 15px;
        top: 15px;
    }

    .styled-select-cat::-ms-expand
    {
        display: none;
    }

    .styled-select-cat select:focus {
        outline: none;
    }

    /* Responsive table cart */
    .table.cart-list,
    .table.cart-list thead,
    .table.cart-list tbody,
    .table.cart-list th,
    .table.cart-list td,
    .table.cart-list tr {
        display: block;
        border: 0;
    }

    .table.cart-list thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

    .table.cart-list tr {
        border: 1px solid #ccc;
    }

    .table.cart-list td {
        border: none;
        border-bottom: 1px solid #ddd;
        position: relative;
        padding-left: 50%;
    }

    .table.cart-list td:before {
        position: absolute;
        top: 12px;
        left: 12px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
    }

    .table.cart-list td:nth-of-type(1):before {
        content: "Item";
        font-weight: bold;
        color: #111;
    }

    .table.cart-list td:nth-of-type(2):before {
        content: "Quantity";
        font-weight: bold;
        color: #111;
    }

    .table.cart-list td:nth-of-type(3):before {
        content: "Discount";
        font-weight: bold;
        color: #111;
    }

    .table.cart-list td:nth-of-type(4):before {
        content: "Total";
        font-weight: bold;
        color: #111;
    }

    .table.cart-list td:nth-of-type(5):before {
        content: "Actions";
        font-weight: bold;
        color: #111;
    }

    .thumb_cart {
        display: none;
    }

    #single_tour_feat ul {
        display: block;
        margin-bottom: 10px;
    }

    #single_tour_feat ul li {
        display: inline-block;
        width: auto;
        font-size: 11px;
    }

    /* Margin tools */
    .margin_60 {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .margin_30 {
        margin-top: 15px;
        margin-bottom: 15px;
    }

    /* Parallax bg */
    .parallax-window,
    #hero_2 {
        height: 240px;
        min-height: 240px;
    }

    .parallax-content-1 {
        height: 240px;
    }

    .parallax-content-1 > div {
        padding: 0 5%;
        font-size: 12px;
        padding-top: 20px;
    }

    .parallax-content-1 div h1,
    .parallax-content-1 div h3 {
        font-size: 20px;
        padding-top: 15px;
    }

    /* Banners */
    #banner {
        padding: 20px;
    }

    .banner a {
        position: relative;
        top: 15px;
        right: 0;
        left: 0;
        margin-bottom: 20px;
    }

    .banner h4 {
        font-size: 22px;
    }

    /* tooltip */
    .strip_all_tour_list .tooltip-content-flip {
        width: 70px;
    }

    /* Single tour page */
    .parallax-content-2 {
        padding-bottom: 15px;
    }

    .parallax-content-2 div h1 {
        font-size: 22px;
    }

    .parallax-content-2 .rating {
        font-size: 12px;
        display: block;
    }

    #price_single_main,
    #price_single_main.hotel {
        font-size: 11px;
        text-align: left;
        padding-top: 0;
    }

    #price_single_main span {
        font-size: 20px;
    }

    #price_single_main sup {
        font-size: 20px;
        top: 0;
    }

    #hero_2 .intro_title h1 {
        font-size: 22px;
    }

    .bs-wizard > .bs-wizard-step .bs-wizard-stepnum {
        font-size: 12px;
    }

    #hero {
        height: 350px;
        font-size: 12px;
    }

    .intro_title h3 {
        font-size: 26px;
        margin-bottom: 5px;
        padding-top:30px;
        color: #fff;
        font-weight: bold;
        text-transform: uppercase;
    }

    .item_cart {
        margin-top: 0;
    }

    .intro_title.error h1 {
        font-size: 50px;
    }

    .radio_fix {
        margin-top: 10px
    }

    .col-md-4.plan:first-child,
    .col-md-4.plan:last-child {
        margin-right: 0px;
        margin-left: 0px;
        border-width: 1px 1px 1px 1px;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .plan-tall + .plan {
        border-left: 1px;
        border: solid #dddddd;
    }

    .plan-tall {
        margin-right: 0;
    }

    #map_filter ul li a {
        min-width: inherit;
    }

    #map_filter ul li a span {
        display: none;
    }

    #map_filter ul li a i {
        font-size: 24px;
    }

    #map_contact {
        height: 300px;
    }

    #map_contact.contact_2 {
        margin-top: 60px;
    }

    .content-left {
        padding-top: 80px;
    }

    .map-right #map{
        height: 375px;
    }

    #top_line.visible_on_mobile{
        display: block;
    }

    a#lang_link,
    a#currency_link {
        position: relative;
        text-indent: -999px;
        width: 20px;
        height: 20px;
        display: block;
    }

    #lang_link:before,
    #currency_link:before {
        text-indent: 0px;
        font-size: 20px;
    }

    .dropdown-access .dropdown-menu:before {
        right: 5%;
    }

    a#access_link,
    a#wishlist_link {
        position: relative;
        text-indent: -999px;
        width: 20px;
        height: 20px;
        display: block;
    }

    #wishlist_link:before,
    #access_link:before {
        text-indent: 0px;
        font-size: 20px;
    }
    .search-overlay-menu form {
        width: 80%;
        left: 10%;
        height: 50px;
        margin: -25px auto 0;
    }
    .search-overlay-menu input[type="search"] {
        font-size: 26px;
    }
    .search-overlay-menu form button {
        font-size: 2rem;
        top: 10px;
    }

    .laptop{
        display: none;
    }

    .score {
        display: none;
    }
}
/* END MEDIA QUERIES max-width 767px - MOBILE LANDSCAPE  */

/* MEDIA QUERIES max-width 480px - MOBILE PORTRAIT  */
@media (max-width: 480px) {

    .features-slider {
        padding: 11% 50px 10% 50px;
    }

    .box_style_1 {
        padding: 15px;
    }

    .box_style_1 h3.inner {
        margin: -15px -15px 15px -15px;
    }

    .img_list img {
        left: 0;
        width: 100%;
        height: auto;
        top: -5%;
    }

    .tooltip-item::after {
        width: 180px;
    }

    .tooltip-content {
        width: 180px;
        margin: 0 0 20px -90px;
    }

    .styled-select-filters {
        margin-bottom: 5px;
    }

    .search_bar {
        width: 100%;
    }

    #hero {
        height: 250px;
    }

    .intro_title {
        display: none;
    }

    .sticky ul#top_tools, ul#top_tools  {
        margin: 3px 25px 0 0;
    }

    .dropdown.dropdown-cart {
        display: none;
    }

    .dropdown.dropdown-search {
        font-size: 20px;
    }

    #testimonials h2 {
        font-size: 28px;
    }

    #score {
        float: none;
        margin-top: 15px;
    }

    .intro_title.error {
        display: table-cell;
    }

    .intro_title.error h1 {
        font-size: 36px;
    }

    .intro_title.error a.button_intro {
        display: none;
    }

    #search {
        padding: 0 5%;
        padding-top: 100px;
    }

    .promo_full_wp div h3 {
        font-size: 36px;
    }

    .promo_full_wp div a i {
        font-size: 60px;
    }

    #map_filter ul li a i {
        font-size: 18px;
    }

    #search ul.nav.nav-tabs{
        margin-left:0;
        padding-left:0;
        margin-bottom: 1px;
    }

    #search .nav-tabs > li > a {
        font-size:11px;
        padding:5px 8px;
    }


}
/* END MEDIA QUERIES max-width 480px - MOBILE PORTRAIT  */