$shape-crnr: 3;

$btn-border-width-xs: 1;
$btn-size-xs: 32;
$btn-font-size-xs: 14;
$shape-crnr-xs: 3;

$btn-border-width-sm: 1;
$btn-size-sm: 36;
$btn-font-size-sm: 16;
$shape-crnr-sm: 5;

$btn-border-width-lg: 2;
$btn-size-lg: 48;
$btn-font-size-lg: 23;
$shape-crnr-lg: 7;

$btn-border-width-xl: 3;
$btn-size-xl: 56;
$btn-font-size-xl: 26;
$shape-crnr-xl: 10;